body {
    background-image: linear-gradient(rgba(0,0,0,0.4),rgba(0,0,0,0.4)),url(bg-img.jpg);
    /* background-image: url("bg-img.jpg"); */
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
  }

.state-wise-data-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .header{
    color: #1cf8f8;
  }
  
  .search-container {
    width: 100%;
    margin-bottom: 20px;
    margin-top: 10px;
  }
  
  input[type='text'] {
    width: 60%;
    padding: 10px;
    border: none;
    border-bottom: 2px solid #ccc;
    outline: none;
    font-size: 16px;
    color: #008080;
    background-color: #c0fafa;
    border-radius: 18px;
  }

  .cards-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }

  .card {
    width: 100%;
    min-width: 350px;
    height: 100%;
    border-radius: 10px;
    box-shadow: 0px 4px 10px rgb(0, 0, 0. 0.1);
    margin: 20px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    transition: all 0.4s ease-in-out;
  }
  
  .card:hover {
    box-shadow: 0px 4px 20px rgba(255, 255, 255, 0.53);
    transform: translateY(-5px);
  }
  
  .card h3 {
    margin: 0px;
    padding: 20px;
    font-size: 22px;
    background: #1010a4;
    font-weight: bold;
    color: aquamarine;
    text-align: center;
  }
  
  .card-body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 18px;
    padding: 20px;
    color: #008080;
    background-color: #c0fafa;
  }
  
  .card-row {
    display: flex;
    justify-content: space-between;
    font-size: 16px;
    color: #555;
  }
  
  .card-row span:nth-child(1) {
    font-weight: bold;
  }
  
  .card-row span:nth-child(2) {
    color: #ff7f50;
    font-weight: bold;
  }
  
  .card-row span:nth-child(3) {
    color: #008080;
    font-weight: bold;
  }
  
  .card-row span:nth-child(4) {
    color: #dc143c;
    font-weight: bold;
  }
  
  .card-row span:nth-child(5) {
    font-weight: bold;
  }

  @media screen and (min-width: 768px) {
    .cards-container {
      justify-content: space-around;
    }
    .card {
      width: calc(30% - 10px);
    }
  }
  